import { Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { LogCliService } from "../../../services/adminService";
import { Box, MyBox } from "../../core/MyBox";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";

export default function LogActionComponent() {
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: number) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "URL", key: "url", render: (data: any, row: any) =>
                <span>{row.httpRequest.path}</span>
        }),
        new TableColumn({
            title: "Method", key: "method", render: (data: any, row: any) =>
                <span>{row.httpRequest.method}</span>
        }),
        new TableColumn({
            title: "Time", key: "time", render: (data: any, row: any) =>
                <span>{row.httpRequest.time}</span>
        }),
        new TableColumn({
            title: "Status code", key: "statusCode", render: (data: any, row: any) =>
                <span>{row.httpRequest.statusCode}</span>
        }),
        new TableColumn({
            title: "Date", key: "date", render: (data: any, row: any) =>
                <UIFormatDateTimeVNComponent value={data} />
        }),
        new TableColumn({ title: "Ip Address", key: "ipAddress" }),
    ];

    const [dayReport, setDayReport] = useState(0);
    const [totalReqDay, setTotalReqDay] = useState(0);
    const [totalTimeDay, setTotalTimeDay] = useState(0);
    const [rqSuccessDay, setReqSuccessDay] = useState(0);
    const [rqErrDay, setRqErrorDay] = useState(0);
    const [percentReqErrSuccessDay, setPercentReqErrSuccessDay] = useState(0);
    const [timeRunFastestDay, setTimeRunFastestDay] = useState(0);
    const [timeRunLowestDay, setTimeRunLowestDay] = useState(0);
    const [timeRunAvgDay, setTimeRunAvgDay] = useState(0);

    const [monthReport, setMonthReport] = useState(0);
    const [totalReqMonth, setTotalReqMonth] = useState(0);
    const [totalTimeMonth, setTotalTimeMonth] = useState(0);
    const [rqSuccessMonth, setReqSuccessMonth] = useState(0);
    const [rqErrMonth, setRqErrorMonth] = useState(0);
    const [percentReqErrSuccessMonth, setPercentReqErrSuccessMonth] = useState(0);
    const [timeRunFastestMonth, setTimeRunFastestMonth] = useState(0);
    const [timeRunLowestMonth, setTimeRunLowestMonth] = useState(0);
    const [timeRunAvgMonth, setTimeRunAvgMonth] = useState(0);

    var loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);

        Promise.all([LogCliService.GetListLogAction(paginationRequest), LogCliService.ReportDetail()])
            .then(([rs1, rs2]) => {
                if (rs1.isSuccess && rs2.isSuccess) {
                    // rs1
                    setPageInfo(new PageInfoModel(rs1));
                    setData(rs1.data.pagedData);

                    //rs2
                    const reportDay = rs2.data.day;
                    setDayReport(reportDay.value);
                    setTotalReqDay(reportDay.totalReq);
                    setTotalTimeDay(reportDay.totalTime);
                    setReqSuccessDay(reportDay.rqSuccess);
                    setRqErrorDay(reportDay.rqErr);
                    setPercentReqErrSuccessDay(reportDay.percentReqErrSuccess);
                    setTimeRunFastestDay(reportDay.timeRunFastest);
                    setTimeRunLowestDay(reportDay.timeRunLowest);
                    setTimeRunAvgDay(reportDay.timeRunAvg);

                    const reportMonth = rs2.data.month;
                    setMonthReport(reportMonth.value);
                    setTotalReqMonth(reportMonth.totalReq);
                    setTotalTimeMonth(reportMonth.totalTime);
                    setReqSuccessMonth(reportMonth.rqSuccess);
                    setRqErrorMonth(reportMonth.rqErr);
                    setPercentReqErrSuccessMonth(reportMonth.percentReqErrSuccess);
                    setTimeRunFastestMonth(reportMonth.timeRunFastest);
                    setTimeRunLowestMonth(reportMonth.timeRunLowest);
                    setTimeRunAvgMonth(reportMonth.timeRunAvg);
                }
            })
            .finally(() => setIsLoading(false));
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    return (
        <MyBox
            title="Log hành động người dùng"
            isLoading={isLoading}>

            <Row gutter={12}>
                <Col span={12}>
                    <Box>
                        <h3>Thống kê theo ngày: {dayReport}</h3>
                        <p>Tổng số request: {Fn.FormatNumber(totalReqDay)}</p>
                        <p>Tổng thời gian chạy: {Fn.FormatNumber(totalTimeDay)}</p>
                        <p>Tổng số request thành công: {Fn.FormatNumber(rqSuccessDay)}</p>
                        <p>Tổng số request lỗi: {Fn.FormatNumber(rqErrDay)}</p>
                        <p>Tỉ lệ request lỗi/ thành công: {percentReqErrSuccessDay}</p>
                        <p>Thời gian chạy nhanh nhất: {Fn.FormatNumber(timeRunFastestDay)}</p>
                        <p>Thời gian chạy chậm nhất: {Fn.FormatNumber(timeRunLowestDay)}</p>
                        <p>Thời gian chạy trung bình: {Fn.FormatNumber(timeRunAvgDay)}</p>
                    </Box>
                </Col>
                <Col span={12}>
                    <Box>
                        <h3>Thống kê theo tháng: {monthReport}</h3>
                        <p>Tổng số request: {Fn.FormatNumber(totalReqMonth)}</p>
                        <p>Tổng thời gian chạy: {Fn.FormatNumber(totalTimeMonth)}</p>
                        <p>Tổng số request thành công: {Fn.FormatNumber(rqSuccessMonth)}</p>
                        <p>Tổng số request lỗi: {Fn.FormatNumber(rqErrMonth)}</p>
                        <p>Tỉ lệ request lỗi/ thành công: {percentReqErrSuccessMonth}</p>
                        <p>Thời gian chạy nhanh nhất: {Fn.FormatNumber(timeRunFastestMonth)}</p>
                        <p>Thời gian chạy chậm nhất: {Fn.FormatNumber(timeRunLowestMonth)}</p>
                        <p>Thời gian chạy trung bình: {Fn.FormatNumber(timeRunAvgMonth)}</p>
                    </Box>
                </Col>
            </Row>
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />

        </MyBox>
    );
}